@import url(https://fonts.googleapis.com/css2?family=Kaisei+Opti:wght@400;700&family=Klee+One&display=swap);
body {
  margin: 0;
  font-family: 'Kaisei Opti', serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #eceff1;
  color: #4d4d4d;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.Card {
  width: 250px;
  padding: 10px;
  margin: 1rem;
  box-shadow: 7px 10px 12px -5px rgba(0, 0, 0, 0.56);
  text-align: center;
  background-color: white;
  border-radius: 3px;
}

.Card-data {
  font-size: 80%;
}

.Card-image {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 200px;
  background-color: #eceff1;
  border-radius: 3px;
  margin-bottom: 1rem;
}

.Card-image img {
  transition: all ease 500ms;
}

.Card-image img:hover {
  -webkit-transform: scale(1.25);
          transform: scale(1.25);
  opacity: .7;
}

.Card-title {
  margin-bottom: 1rem;
  font-weight: bold;
  font-size: 1.5rem;
}
.Pokedex {
  text-align: center;
}

.Pokedex-cards {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
}

.Pokedex-winner {
color: #004e6b;
}

.Pokedex-loser {
  color: #6b0000;
}

